.editor-container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:stretch;
    overflow: none;
    background-color: black;
}

.editing-canvas-holder {
    position: relative;
    flex-grow: 9;
    align-self: stretch;
    flex-basis: 200px;
    border: 2px solid white;
}

.editing-canvas {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}

.editing-buttons {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    justify-self: stretch;
    align-items: center;
    color: black;
}

.edit-button {
    border-radius: 5px;
    background-color: black;
    border: none;
    padding: 5px;
    margin-right: 2px;
    color: black;

	&:hover:not(.disabled) {
	    transition: background-color 0.25s;
	    background-color: rgb(220, 220, 220);
	}

	&:active:not(.disabled) {
	    transition: background-color 0.1s transform 0.1s;
	    background-color: black;
	    transform: translateY(2px);
	}

	&.selected:hover {
	    transition: background-color 0.25s;
	    background-color: rgb(110, 110, 110);
	}

	&.selected {
	    background-color: rgb(50, 50, 50);
	}
	&.disabled {
	    background-color: darkslategray;
	    color: lightslategray;
	}

	&.disabled:hover {
	    background-color: rgb(10, 10, 10);
	    color: rgb(30, 30, 30);
	}

	&.disabled:active {
	    background-color: crimson;
	    color: red;
	}
}

.tile-picker {
    display: flex;
    flex-direction: row;
}

.saved-tile-selection-button {
    color: white;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 16px;
    border: 1px solid white;
    margin-left: 2px;
    margin-right: 2px;

    &.selected {
        background-color: rgb(60, 60, 60);
        color: green;
        font-weight: bold;
    }

    &.unselected {
        background-color: rgb(10, 10, 10);
    }
}


.editor-tile-creator {
    display: flex;
    flex-direction: column;
    padding: 10px;
    background-color: rgb(10, 10, 10);
    border-radius: 10px;
    border: 1px solid black;
	& > * {
	    margin-top: 5px;
	}
}

.tile-creator-container {
    transform: translateX(100%);
    transition: transform 0.25s;
    position: absolute;
    right: 0px;
    top: 0px;

    opacity: 0;
    pointer-events: none;
    touch-action:none;
	&.opened {
        transition: transform 0.25s opacity 0.25s;
        opacity: 1;
	    transform: translateX(0);
        pointer-events: auto;
        touch-action: auto;
	}
}


.editor-tile-creator-open-button {
    position: absolute;
    background: none;
    border: none;
    color: white;
    font-size: 40px;
    width: 50px;
    height: 50px;
    left: 0px;
    top: 0px;
    opacity: 1;
    transform: translateX(-100%);
}


.tile-creator-button {
    color: white;
    font-family: monospace;
    background-color: black;
    border: none;
    border-radius: 5px;
	&:hover {
	    transition: transform 0.25s;
	    background-color: darkslategray;
	    transform: translateY(-5px) scale(1.05) skew(5px);
	}
}

.sketch-picker {
    background-color: black;
}


.tile-creator-save-button {
    background-color: green !important;
}

.map-generator {
    display: flex;
    flex-direction: column;
}

.map-dimensions-input {
    display: flex;
    flex-direction: row;
}

.map-dimensions-input input {
    background-color: black;
    color: white;
    text-align: center;
    font-family: monospace;
    caret-color: green;
}

.map-generate-button {
    color: white;
    text-align: center;
    background-color: black;
    border: 2px solid white;
    align-self: center;
    justify-self: center;

    &:hover {
        color: lightgreen;
    }

    &:active {
        color: green;
    }
}
